'use client';

/**
 * PhishGuard Dashboard - Single Pane of Glass
 * 
 * Main dashboard page with 3-column layout:
 * - Left: Navigation & User Profile
 * - Center: Scanner & Live Feed
 * - Right: Governance Widget & Threat Map
 */

import { useState } from 'react';
import { useQuery } from '@tanstack/react-query';
import Link from 'next/link';
import { ScanInput } from '@/components/scan-input';
import { LiveFeed } from '@/components/live-feed';
import { ThreatMap } from '@/components/threat-map';
import { GovernanceWidget } from '@/components/governance-widget';
import { SettingsModal } from '@/components/settings-modal';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Card, CardContent } from '@/components/ui/card';
import { Separator } from '@/components/ui/separator';
import {
    Shield,
    Scan,
    History,
    Globe,
    Settings,
    Moon,
    Sun,
    User,
    Bell,
    Menu,
    X,
    ChevronRight,
    Zap,
    Activity,
} from 'lucide-react';
import { cn } from '@/lib/utils';
import { checkHealth } from '@/lib/api';
import { useTheme } from 'next-themes';
import type { ScanResponse } from '@/types';

export default function Dashboard() {
    const [activeNav, setActiveNav] = useState<'scan' | 'history' | 'map' | 'settings'>('scan');
    const [mobileNavOpen, setMobileNavOpen] = useState(false);
    const [recentScans, setRecentScans] = useState<ScanResponse[]>([]);
    const { theme, setTheme, resolvedTheme } = useTheme();

    // Health check
    const { data: health } = useQuery({
        queryKey: ['health'],
        queryFn: checkHealth,
        refetchInterval: 30000,
        retry: 1,
    });

    const handleScanComplete = (result: ScanResponse) => {
        setRecentScans(prev => [result, ...prev].slice(0, 10));
    };

    const navItems = [
        { id: 'scan' as const, label: 'Scanner', icon: Scan },
        { id: 'history' as const, label: 'History', icon: History },
        { id: 'map' as const, label: 'Threat Map', icon: Globe },
        { id: 'settings' as const, label: 'Settings', icon: Settings },
    ];

    return (
        <div className="min-h-screen bg-gradient-to-br from-slate-950 via-slate-900 to-indigo-950">
            {/* Header */}
            <header className="sticky top-0 z-50 border-b border-slate-800/50 bg-slate-950/80 backdrop-blur-xl">
                <div className="flex h-16 items-center justify-between px-4 lg:px-6">
                    {/* Logo */}
                    <div className="flex items-center gap-3">
                        <Button
                            variant="ghost"
                            size="icon"
                            className="lg:hidden"
                            onClick={() => setMobileNavOpen(!mobileNavOpen)}
                        >
                            {mobileNavOpen ? <X className="h-5 w-5" /> : <Menu className="h-5 w-5" />}
                        </Button>
                        <Link href="/" className="flex items-center gap-2">
                            <div className="relative">
                                <Shield className="h-8 w-8 text-indigo-500" />
                                <div className="absolute -bottom-1 -right-1 h-3 w-3 rounded-full bg-green-500 ring-2 ring-slate-950" />
                            </div>
                            <div>
                                <h1 className="text-xl font-bold tracking-tight">
                                    Phish<span className="text-indigo-400">Guard</span>
                                </h1>
                                <p className="text-[10px] text-slate-500 uppercase tracking-wider hidden sm:block">
                                    Security Dashboard
                                </p>
                            </div>
                        </Link>
                    </div>

                    {/* Status Badges */}
                    <div className="hidden md:flex items-center gap-2">
                        {health?.status === 'healthy' && (
                            <Badge variant="outline" className="gap-1 bg-green-500/10 text-green-400 border-green-500/30">
                                <Activity className="h-3 w-3" />
                                System Healthy
                            </Badge>
                        )}
                        <Badge variant="outline" className="gap-1 bg-indigo-500/10 text-indigo-400 border-indigo-500/30">
                            <Zap className="h-3 w-3" />
                            ML Active
                        </Badge>
                    </div>

                    {/* Right Actions */}
                    <div className="flex items-center gap-2">
                        <Button
                            variant="ghost"
                            size="icon"
                            className="h-9 w-9"
                            onClick={() => setTheme(resolvedTheme === 'dark' ? 'light' : 'dark')}
                        >
                            {resolvedTheme === 'dark' ? (
                                <Sun className="h-4 w-4" />
                            ) : (
                                <Moon className="h-4 w-4" />
                            )}
                        </Button>
                        <Button variant="ghost" size="icon" className="h-9 w-9">
                            <Bell className="h-4 w-4" />
                        </Button>
                        <SettingsModal />
                        <Separator orientation="vertical" className="h-6 hidden sm:block" />
                        <div className="hidden sm:flex items-center gap-2">
                            <div className="h-8 w-8 rounded-full bg-gradient-to-br from-indigo-500 to-purple-600 flex items-center justify-center">
                                <User className="h-4 w-4 text-white" />
                            </div>
                        </div>
                    </div>
                </div>
            </header>

            <div className="flex">
                {/* Sidebar Navigation - Desktop */}
                <aside className={cn(
                    "fixed inset-y-0 left-0 z-40 w-64 border-r border-slate-800/50 bg-slate-950/95 backdrop-blur-xl pt-16 transition-transform lg:translate-x-0 lg:static lg:pt-0",
                    mobileNavOpen ? "translate-x-0" : "-translate-x-full"
                )}>
                    <nav className="flex flex-col h-full p-4">
                        <div className="space-y-1">
                            {navItems.map((item) => (
                                <Button
                                    key={item.id}
                                    variant={activeNav === item.id ? 'secondary' : 'ghost'}
                                    className={cn(
                                        "w-full justify-start gap-3",
                                        activeNav === item.id && 'bg-indigo-500/10 text-indigo-400 hover:bg-indigo-500/20'
                                    )}
                                    onClick={() => {
                                        setActiveNav(item.id);
                                        setMobileNavOpen(false);
                                    }}
                                >
                                    <item.icon className="h-4 w-4" />
                                    {item.label}
                                    {activeNav === item.id && (
                                        <ChevronRight className="h-4 w-4 ml-auto" />
                                    )}
                                </Button>
                            ))}
                        </div>

                        {/* User Profile Card */}
                        <div className="mt-auto">
                            <Card className="bg-slate-900/50 border-slate-800/50">
                                <CardContent className="p-4">
                                    <div className="flex items-center gap-3">
                                        <div className="h-10 w-10 rounded-full bg-gradient-to-br from-indigo-500 to-purple-600 flex items-center justify-center">
                                            <User className="h-5 w-5 text-white" />
                                        </div>
                                        <div className="min-w-0">
                                            <p className="text-sm font-medium truncate">Security Admin</p>
                                            <p className="text-xs text-slate-500 truncate">admin@phishguard.io</p>
                                        </div>
                                    </div>
                                </CardContent>
                            </Card>
                        </div>
                    </nav>
                </aside>

                {/* Mobile overlay */}
                {mobileNavOpen && (
                    <div
                        className="fixed inset-0 z-30 bg-black/50 lg:hidden"
                        onClick={() => setMobileNavOpen(false)}
                    />
                )}

                {/* Main Content */}
                <main className="flex-1 p-4 lg:p-6 min-h-[calc(100vh-4rem)]">
                    <div className="grid gap-4 lg:gap-6 lg:grid-cols-3">
                        {/* Center Column - Scanner & Feed */}
                        <div className="lg:col-span-2 space-y-4 lg:space-y-6">
                            {/* Scanner */}
                            <ScanInput onScanComplete={handleScanComplete} />

                            {/* Live Feed */}
                            <LiveFeed />

                            {/* Recent Scans (from this session) */}
                            {recentScans.length > 0 && (
                                <Card className="bg-slate-900/50 border-slate-800/50">
                                    <CardContent className="p-4">
                                        <h3 className="text-sm font-medium mb-3 flex items-center gap-2">
                                            <History className="h-4 w-4 text-indigo-500" />
                                            Recent Scans (This Session)
                                        </h3>
                                        <div className="space-y-2">
                                            {recentScans.slice(0, 5).map((scan, i) => (
                                                <div
                                                    key={`${scan.url}-${i}`}
                                                    className={cn(
                                                        "flex items-center justify-between rounded-lg border p-3",
                                                        scan.verdict === 'SAFE' && 'border-green-500/30 bg-green-500/5',
                                                        scan.verdict === 'PHISHING' && 'border-red-500/30 bg-red-500/5',
                                                        scan.verdict === 'SUSPICIOUS' && 'border-yellow-500/30 bg-yellow-500/5'
                                                    )}
                                                >
                                                    <div className="min-w-0">
                                                        <p className="text-sm truncate">{scan.url}</p>
                                                        <p className="text-xs text-slate-500">
                                                            {scan.latency_ms}ms • Risk: {scan.risk_score}%
                                                        </p>
                                                    </div>
                                                    <Badge
                                                        variant="outline"
                                                        className={cn(
                                                            'ml-2 shrink-0',
                                                            scan.verdict === 'SAFE' && 'bg-green-500/10 text-green-400 border-green-500/30',
                                                            scan.verdict === 'PHISHING' && 'bg-red-500/10 text-red-400 border-red-500/30',
                                                            scan.verdict === 'SUSPICIOUS' && 'bg-yellow-500/10 text-yellow-400 border-yellow-500/30'
                                                        )}
                                                    >
                                                        {scan.verdict}
                                                    </Badge>
                                                </div>
                                            ))}
                                        </div>
                                    </CardContent>
                                </Card>
                            )}
                        </div>

                        {/* Right Column - Governance & Threat Map */}
                        <div className="space-y-4 lg:space-y-6">
                            {/* Governance Widget */}
                            <GovernanceWidget />

                            {/* Threat Map */}
                            <ThreatMap compact />
                        </div>
                    </div>
                </main>
            </div>
        </div>
    );
}
